
.TodoSearch {
    background: #F9FBFC;
    border-radius: 10px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    margin-top: 10px;
  }
  
.TodoSearch::placeholder{
    color: brown;
    font-family: 'Montserrat';
}

.TodoSearch:focus{
    outline-color: #ad899b;
    border: 6px solid #957685;
}
.TodoSearch:disabled{
    opacity: 80%;
}